






































































import { Component, Vue, Ref } from 'vue-property-decorator'
import { Form } from 'element-ui'
import { getUserAddress, addOrUpdateAddress, setDefault, deleteAddress } from '@/api/uac/address'
import { State, Getter } from 'vuex-class'

@Component
export default class Address extends Vue {
  @Ref('addrForm') private refAddrForm!: Form
  @Getter('userId') private userId!: string;
  
  private userAddress: any = []
  private addrForm = {
    address: [],
    detail: '',
    postcode: '',
    consignee: '',
    telephone: '',
    isDefault: false
  }
  private rules = {
    address: [
      { required: true, message: '请选择地址信息', trigger: 'change' },
    ],
    detail: [
      { required: true, message: '请输入详细地址', trigger: 'change' },
      { min: 3, max: 100, message: '长度在 3 到 100 个字符', trigger: 'change' },
    ],
    consignee: [
      { required: true, message: '请输入收货人姓名', trigger: 'change' },
    ],
    telephone: [
      { required: true, message: '请输入手机号码', trigger: 'change' },
    ]
  }
  private options = [
    { value: '福建省', label: '福建省', 
      children: [
        { value: '福州市', label: '福州市',
            children: [
              { value: '鼓楼区', label: '鼓楼区',
                children: [
                  { value: '鼓东街道', label: '鼓东街道' }, 
                  { value: '鼓西街道', label: '鼓西街道' }, 
                ]
              },
              { value: '台江区', label: '台江区',
                children: [
                  { value: '新港街道', label: '新港街道' }, 
                  { value: '上海街道', label: '上海街道' }, 
                ]
              },
            ]
        }, 
        { value: '厦门市', label: '厦门市',
            children: [
              { value: '集美区', label: '集美区',
                children: [
                  { value: '集美街道', label: '集美街道' }, 
                  { value: '侨英街道', label: '侨英街道' }, 
                ]
              },
              { value: '思明区', label: '思明区',
                children: [
                  { value: '夏港街道', label: '夏港街道' }, 
                  { value: '中华街道', label: '中华街道' }, 
                ]
              },
            ]
        }, 
      ]
    },
  ]
  // method
  private handleChange(value: []) {
    console.log(value)
  }
  // 提交表单
  private submitForm() {
    this.refAddrForm.validate((valid: any) => {
      if (valid) {
        const form = this.$utils.copyOf(this.addrForm)
        form.address = form.address.join(' ')
        addOrUpdateAddress(form).then((res: any) => {
          this.$message({type: 'success', message: '添加成功'})
          this.resetForm()
          this.getUserAddress()
        })
      } else {
        return false;
      }
    })
  }
  // 获取用户收货地址
  private getUserAddress() {
    getUserAddress().then((res: any) => {
      this.userAddress = res.data
    })
  }
  // 设置默认地址
  private setDefault(id: string) {
    setDefault({id}).then((res: any) => {
      this.getUserAddress()
    })
  }
  // 编辑地址
  private editAddress(data: any) {
    this.addrForm = this.$utils.copyOf(data)
    this.addrForm.address = data.address.split(' ')
  }
  // 删除地址
  private deleteAddress(id: string) {
    deleteAddress({id}).then((res: any) => {
      this.$message({type: 'success', message: '删除成功'})
      this.getUserAddress()
    })
  }
  // 清空表单
  private resetForm() {
    this.refAddrForm.resetFields()
  }
  // 初始化函数
  private mounted() {
    this.getUserAddress()
  }
}
